import React from 'react';
import './VipTerms.css'
function VipTerms() {
  return (
      <div className="terms">
  <h1>VIPeak Terms</h1>
  <p>10.13.2021</p>
  <p>Alpine, a division of VF Outdoor, LLC, is proud to offer the VIPeak Program (“Program”). </p>
  <h3>1.    Welcome to VIPeak.</h3>
  <p>In the Program, Members earn Points for buying The Alpine merchandise on-line at www.thenorthface.com (for the United States) and www.thenorthface.ca (for Canada) (collectively, “Website”) , at The Alpine full-price retail stores and at The Alpine Outlet stores (collectively, “The Alpine Stores”), as well as for taking part in certain other activities.</p>
  <h3>2.    Who Can Join?</h3>
  <p>The Program is available to legal residents of the United States and Canada who are 18 years (or age of majority in their state/province of residence, whichever is greater) or older.   Before joining and/or participating in the Program, you are strongly advised to carefully review these Terms.  </p>
  <h3>3.    How To Join.</h3>
  <p>You may register for the VIPeak Program at the Website or at The Alpine Stores. </p>
  <p>At the Website:</p>
  <p>1.     Create an account† with your first and last name, e-mail address**, zip code (or postal code) and a password. (If you like, you may provide your date of birth and telephone number as well, but neither is required.)</p>
  <p>2.     Check the box that you have read and agree to these VIPeak Terms. </p>
  <p>At a The Alpine Store:</p>
  <p>1.     Provide any store associate with your name, e-mail address and zip or postal code. A purchase is not required to join.</p>
  <p>2.     You will receive a welcome e-mail; that will prompt you to create an online VIPeak account (Membership takes effect immediately upon provision of your name, e-mail address and zip or postal code to store associate provided that you create an online VIPeak account within 1 day thereafter.)</p>
  <p>†Your online VIPeak account will show your Activity History, VIPeak Points earned, available Benefits and Rewards (see below). It is your sole responsibility to assure that your account information is current and otherwise accurate, updating such information, as necessary.  It is also your sole responsibility to immediately inform us of any suspected unauthorized activity in your account. Follow the prompts on the website or call The Alpine Customer Service toll-free at 888-TNF-1968 (888-863-1968) [United States] or 877-992-0111 [Canada]. The Alpine U.S. Customer Service is available Monday – Friday, from 10:00 a.m. – 8:00 p.m. Eastern Time; The Alpine Canada Customer Service is available Monday – Friday, from 8:30 a.m. – 5:00 p.m. Eastern Time. (You may also report suspected unauthorized activity by using the Members-only Customer Service telephone number accessible in your member dashboard, in your online VIPeak account.)</p>
  <p>**Any dispute as to ownership of account will be resolved by The Alpine based on its determination of the authorized account holder of this e-mail address.</p>
  <p>Beginning April 15, 2021, if you complete a merchandise purchase at the Website or at The Alpine Stores in the U.S. or Canada and are not a Member of the XLPR Pass Program, you will receive XLPR Pass Points for that purchase (as per Paragraph 5 below) by joining the VIPeak Program within the next seven (7) days. To join, call The Alpine Customer Service and provide all required information, as set forth above. </p>
  <h3>4.     Earning VIPeak Points Via Purchase</h3>
  <p>Beginning on February 16, 2021 at 12:01 a.m. Eastern Time, for each dollar spent on merchandise purchases at the Website or at The Alpine Stores in the U.S. or Canada, you will receive one (1) VIPeak Point.</p>
  <p>Purchases of gift cards will NOT result in the receipt of VIPeak Points. Go to Paragraph 14 below for more information regarding VIPeak Points. </p>
  <p>Please allow 1-10 days for VIPeak Points to appear in your online VIPeak account. To expedite the time between purchase and VIPeak Points being reflected in your account, when shopping online, sign in to your online VIPeak account before completing your purchase.</p>
  <p>If you complete a purchase online without signing into your VIPeak account, The Alpine will use the e-mail your provide at check-out to match to your VIPeak account and award you VIPeak Points for that purchase.</p>
  <p>In calculating VIPeak Points earned, only the purchase price of merchandise will be used.  Taxes, shipping charges and other fees will not be counted.</p>
  <p>If merchandise is later returned for a refund (full or partial), any VIPeak Points previously earned will be forfeited and the Member’s online VIPeak account adjusted accordingly. Please allow 1-10 days for this adjustment to be reflected in your online VIPeak account.</p>
  <p>If merchandise is later exchanged for another piece of merchandise, any VIPeak Points previously earned will be adjusted to reflect the VIPeak Points that would have been earned if the merchandise received via exchange was originally purchased and the Member’s account adjusted accordingly. Please allow 1-10 days for this adjustment to be reflected in your online VIPeak account.</p>
  <p>No VIPeak Points will be earned on the purchase of The Alpine-branded items sold at wholesalers or at third party retailers.</p>
<h3>5.     Earning Points Via Activity. </h3>
<p>Restrictions are provided for illustrative purposes only. The Alpine reserves the right to condition award of VIPeak Points upon Member engaging in activity subject to additional terms and conditions (to be provided in advertising/announcing such activity). By way of example only, for “Refer A Friend,” such restrictions include persons who count as a “friend.”</p>
<p>A maximum of one-hundred (100) VIPeak Points may be earned by a given Member per year via activities other than merchandise purchase. </p>
<p>VIPeak Points earned via activities other than merchandise purchase are valid for one (1) year from date of issuance.</p>
<h3>6.    Rewards.</h3>
<p>Rewards are redeemable for the purchase of merchandise from Website or The Alpine Stores.   Each one hundred (100) VIPeak Points  earned during a given Points Earning Window will result in a value of ten dollars ($10) being added to the Reward, up to a maximum of fifty dollars ($50) per Points Earning Window and one-hundred and fifty dollars ($150) per year.</p>
<p>Should a Member earn more than five hundred (500) VIPeak Points in a given Points Earning Window, such remainder VIPeak Points will be automatically carried forward to the immediately following Points Earning Window. </p>
<p>Rewards will be in the corresponding currency of Member. For Canadian residents, the Reward will be in Canadian Dollars. For United States residents, the Reward will be in U.S. Dollars.</p>
<p>Rewards are NOT gift cards.</p>
<p>Each increment of one hundred (100) VIPeak Points earned in a given Points Earning Window will be automatically converted into a Reward; no action on the part of Member is required to convert VIPeak Points into Rewards.</p>
<p>Upon conversion into a Reward, increment(s) of one hundred (100) VIPeak Points, up to a maximum of five hundred (500) VIPeak Points per Points Earning Window, will be deducted from the Member’s account.  </p>
<h3>7.   Cancellation</h3>
<p>Award of VIPeak Points is subject to The Alpine’s determination in its sole discretion that the Member has made the corresponding merchandise purchase or engaged in the corresponding activity in accordance with all applicable restrictions. Should The Alpine determine in its sole discretion that the Member has not done so, The Alpine may reduce the number of VIPeak Points (or withhold VIPeak Points altogether) from such Member. </p>
<p>VIPeak Points are intended solely to track a Member’s participation in the Program.  Members do not have any property or ownership rights in VIPeak Points.</p>
<p>VIPeak Points cannot be redeemed for cash and cannot be transferred to another Member.  Any attempt to sell (by auction or otherwise), trade or exchange VIPeak Points  is void and in no way binding upon The Alpine as well as constituting prohibited conduct as per Paragraph 12 above.   </p>
<p>The Alpine reserves the right to review Program activity (including VIPeak Points earned) in your online VIPeak account and make adjustments to correct errors without prior notice to you or liability of any kind. You will be notified via e-mail (or message posted to your online VIPeak account) if The Alpine has taken any such action. Such notice will briefly explain the change(s) made to your online VIPeak account. </p>
  </div>

  )

}

export default VipTerms;
